import DataViewBase from "views/sections/dataview";
import { menuIcon, folderCardIcon } from "../helpers/icons";

export default class CardsView extends DataViewBase {
	config() {
		const compact = this.getParam("compact", true);

		const skin = webix.skin.$active;
		const itemSize =
			skin.listItemHeight < 29
				? {
						height: 151,
						width: 184,
				  }
				: {
						height: 200,
						width: 240,
				  };

		return {
			view: "dataview",
			localId: "cards",
			prerender: compact,
			drag: !compact,
			multiselect: true,
			css: "webix_noselect webix_fmanager_cards",
			template: obj => this.CardTemplate(obj),
			type: {
				...itemSize,
				type: "tiles",
				templateStart: webix.template(
					`<div
class="{common.classname()}" {common.aria()}
style="width: {common.width}px; height: {common.height}px; float: left; overflow: hidden; ">`
				),
			},
			on: {
				onItemDblClick: () => this.Activate(this.GetSelection()),
				onEnter: () => this.Activate(this.GetSelection()),
				onBeforeDrag: ctx => this.DragMarker(ctx),
				onBeforeDrop: ctx => this.MoveFiles(ctx.source, ctx.target),
			},
			onClick: {
				webix_fmanager_card: (e, id) => this.SelectItem(e, id),
				webix_fmanager_menu_icon: e => {
					this.Menu.Show(e);
				},
			},
			onContext: {},
		};
	}
	init() {
		this.WTable = this.$$("cards");
		super.init();
	}
	RenderData(data) {
		this.WTable.sync(data, () => this.WTable.filter(f => f.value !== ".."));
	}
	CardTemplate(obj) {
		const _ = this.app.getService("locale")._;

		let preview, panel;
		if (obj.type === "folder") {
			preview = `<div class="webix_fmanager_card_preview">${folderCardIcon}</div>`;

			panel = `<div class="webix_fmanager_card_panel">
		<span class="webix_fmanager_card_label">${_("Folder")}</span>
		<span class="webix_fmanager_card_name folder">${obj.value}</span>${menuIcon}
		</div>`;
		} else {
			const skin = webix.skin.$active;
			const picSize = skin.listItemHeight < 29 ? [163, 92] : [214, 124];

			const origin = this.app
				.getService("backend")
				.previewURL(obj, picSize[0], picSize[1]);
			const img = `<img height="${picSize[1]}" width="${
				picSize[0]
			}" src="${origin}" onerror='this.style.display="none"'/>`;

			preview = `<div class="webix_fmanager_card_preview file">${img}</div>`;

			const fileIcon = this.Icon(obj);

			panel = `<div class="webix_fmanager_card_panel file">
		<span class="webix_fmanager_card_name">${fileIcon}<span class="file_name_text">${obj.value}</span></span>${menuIcon}
		</div>`;
		}

		return `<div class="webix_fmanager_card_outer"><div class="webix_fmanager_card" webix_l_id="${
			obj.id
		}">${preview + panel}</div></div>`;
	}
	SelectItem(e, id) {
		const table = this.WTable;
		const ctrl = e.ctrlKey || e.metaKey;
		const range = e.shiftKey;

		const select = table.getSelectedId(true);

		if (select.length && range) {
			let start = table.getIndexById(select[select.length - 1]);
			let end = table.getIndexById(id);

			if (start === end) return;

			if (start > end) [start, end] = [end, start];

			const files = table.data.order.slice(start, end + 1);
			table.select(files, true);
		} else {
			if (table.isSelected(id) && ctrl) table.unselect(id);
			else table.select(id, ctrl);
		}
	}
}
